import './App.css';
import { useRef, useState } from 'react';
import db, { storage } from './utils/firebase';
import moment from 'moment';
import BarLoader from "react-spinners/BarLoader";
import { compressImage } from './utils/imageHelper';
import UploadProgress from './components/UploadProgress/UploadProgress';
import { useLongPress } from 'use-long-press';

const ChatImage = ({ imageUrl, fileName, messageId }) => {

  const deleteConfirmHandler = () => {
    if (window.confirm('Are you sure to delete the image') == true) {
      storage.ref('images').child(fileName).delete().then(() => {
        db.collection('messages').doc(messageId).delete().then(() => {
        }).catch((error) => {
          console.error("Error removing document: ", error);
        })
      })
    }
  }
  const clickHandler = useLongPress(() => {
    navigator.vibrate(200);
    deleteConfirmHandler();
  }, { threshold: 500 });

  return (
    <div>
      <img className='chatImage' src={imageUrl} alt={`data-${messageId}`} {...clickHandler()} />
    </div>
  )
}

function App() {
  const [user, setUser] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');
  const [isLogin, setIsLogin] = useState(true);
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [uploadingProgress, setUploadingProgress] = useState(undefined)
  const fileRef = useRef()


  const login = () => {
    setLoading(true);
    if (user && password) {
      db.collection('users')
        .where('user', '==', user)
        .where('password', '==', password)
        .get()
        .then(querySnapshot => {
          setLoading(false);
          querySnapshot.docs.map(doc => {
            setIsLogin(false);
            getMessagesFromDB();
          })
        })
        .catch((err) => {
          setLoading(false);
        })
    }
  }

  const sendMessage = () => {
    if (message.trim()) {
      db.collection('messages')
        .add({ user: user, message: message, createdAt: Date.now() })
        .then(() => {
          setMessage('')
        })
    }
  }

  const getMessagesFromDB = () => {
    db.collection('messages')
      .orderBy('createdAt', 'desc')
      .limit(100)
      .onSnapshot(querySnapshot => {
        const data = querySnapshot.docs.map(doc => {
          return { id: doc.id, ...doc.data() };
        })
        setMessages(data);
      })
  }

  const attachmentClickHandler = () => {
    fileRef.current.click()
  }

  const fileChangeHandler = async (event) => {
    try {
      const fileName = event.target.files[0].name;
      setUploadingProgress(0);
      const compressedImage = await compressImage(event);
      const uploadtask = storage.ref(`images/${fileName}`).put(compressedImage);
      uploadtask.on('state_changes', (snapshot) => {
        // Observe state change events such as progress, pause, and resume
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        setUploadingProgress(Math.ceil(progress))
        switch (snapshot.state) {
          case 'paused':
            console.log('Upload is paused');
            break;
          case 'running':
            console.log('Upload is running');
            break;
          default:
            return
        }
      },
        (error) => { setUploadingProgress(undefined); },
        () => {
          storage.ref('images').child(fileName).getDownloadURL().then((url) => {
            db.collection('messages')
              .add({ user: user, message: url, createdAt: Date.now(), image: true, fileName: fileName })
          }).finally(() => setUploadingProgress(undefined));

        }
      )
    }
    catch {
      alert('Image Compression failed')
    }

  }


  return (
    <>
      {uploadingProgress !== undefined && <UploadProgress progress={uploadingProgress} />}
      {isLogin && <div className="app-container">
        <div className="login-section">
          <div className="input-container">
            <input type="text" placeholder="User Name" value={user} onChange={(e) => setUser(e.target.value)} />
            <input type="password" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)} />
            <button className="login-button" disabled={loading} onClick={login}>Sign In {loading && <i class="fa fa-spin fa-circle-o-notch" aria-hidden="true"></i>}</button>
          </div>
        </div>
      </div>}

      {!isLogin &&
        <div className="chat-container">
          <div className="chat-messages">
            {messages.map((item, index) => {
              return (
                <>
                  <div className={item.user == user ? "message-bubble me right" : "message-bubble them left"}><span className="message">{item.image ? <ChatImage fileName={item.fileName} imageUrl={item.message} messageId={item.id} /> : item.message}</span><div className="time">{moment(item.createdAt).calendar(null, {
                    lastDay: '[Yesterday]',
                    sameDay: '[Today]',
                    lastWeek: 'DD/MM/YYYY',
                    sameElse: 'DD/MM/YYYY'
                  })}, {moment(item.createdAt).format('h:mm A')}</div></div>
                </>
              )
            })}
          </div>
          <div className="chat-input">
            <>
              <input type='file' ref={fileRef} onChange={fileChangeHandler} accept="image/*" />
              <i class="fa fa-paperclip attachment" onClick={attachmentClickHandler} aria-hidden="true"></i>
            </>
            <input type="text" value={message} onChange={(e) => { setMessage(e.target.value) }} placeholder="Type something..." onKeyUp={(e) => { if (e.key === 'Enter') { sendMessage() } }} />
            <button className="chat-send" onClick={sendMessage}><i class="fa fa-lg fa-paper-plane" aria-hidden="true"></i></button>
          </div>
        </div>
      }

    </>
  );
}

export default App;
