import React from 'react'
import './uploadprogress.css'

const UploadProgress = ({ progress }) => {

    return (
        <div className='progressContainer'>
            {progress !== 0 && <><div className='progressTitle'>Uploading Image</div><div className='progressPercentage'>{progress}%</div></>}
            {progress === 0 && <><div className='progressTitle'>Compressing Image</div><div className='progressPercentage'>💾</div></>}
        </div>
    )
}

export default UploadProgress;